* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color:white;
 }
 body {
  background: linear-gradient(90deg, #00d2ff 0%, #3a47d5 100%);
  margin: auto;
 }

.navbar a {
  text-decoration: none;
}
.navbar a:hover {
  
  color:black;
}


.navbar li {
  list-style: none;
}

.navbar ul {
  margin: auto;
  width: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-template-rows: repeat(minmax, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
text-align: center;
padding: 5px;
}


.okolo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  -webkit-box-shadow: 0px 0px 15px 0px #000000; 
box-shadow: 0px 0px 15px 0px #000000;
background: rgba( 255, 255, 255, 0.20 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 20.0px );
-webkit-backdrop-filter: blur( 20.0px );
border-radius: 10px;
width: 80%;


}

.okolo h1 {
  text-align: center;
  font-size: 1.5rem;
}

.line {
  height: 1px;
  background: rgb(255, 255, 255);
  border: none;
  width: 100%;
}

.cart img {
  width: 300px;
  padding:20px;
}
.cart {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: auto;
  -webkit-box-shadow: 0px 0px 15px 0px #000000; 
  box-shadow: 0px 0px 15px 0px #000000;
  background: rgba( 255, 255, 255, 0.20 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 20.0px );
  -webkit-backdrop-filter: blur( 20.0px );
  border-radius: 10px;
  padding: 20px;
}

.card {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
grid-template-rows: repeat(minmax, 1fr);
grid-column-gap: 10px;
grid-row-gap: 30px;

padding: 20px;
margin: auto;

}


